import {Config} from './Config.js';
import {$} from './utils.js';
import {HTML} from './HTML.js';
import {PatternManager} from "./PatternManager.js";

export class EventHandler {
    #app;

    getApp() {
        return this.#app;
    }

    constructor(app) {
        this.#app = app;
        this.initialize();
    }

    /**
     * Met  jour l'tat du bouton de dmarrage.
     *
     * @param {boolean} [forceStop=false] - Si vrai, force l'arrt de l'animation mme si elle n'est pas en cours.
     * @returns {void}
     */
    updateStartButton(forceStop = false) {
        const playIcon = $('#startIcon');
        const pauseIcon = $('#pauseIcon');

        const isPlaying = playIcon.style.display === 'none';

        if (forceStop && !isPlaying) return;

        playIcon.style.display = isPlaying ? 'block' : 'none';
        pauseIcon.style.display = isPlaying ? 'none' : 'block';
        HTML.startPrincipalButton.title = isPlaying ? 'Dmarrer' : 'Pause';

        // Appelle toggleAnimation si l'tat change
        this.getApp().toggleAnimation();
    }

    /**
     * Met  jour les poids des entres alatoires.
     *
     * @param {HTMLInputElement} changedInput - L'entre dont la valeur a chang.
     * @returns {void}
     */
    updateWeights(changedInput) {
        const total = Array.from(HTML.weightFullRandomInputs).reduce((acc, input) => acc + parseFloat(input.value), 0);

        let totalZero = (1 - total).toFixed(2);
        if (totalZero < 0) totalZero = 0;

        HTML.weightRandomSpan0.textContent = totalZero;
        HTML.weightRandomInput0.value = totalZero;

        if (total > 1) {
            const decreaseAmount = (total - 1) / (HTML.weightFullRandomInputs.length - 1);
            HTML.weightFullRandomInputs.forEach(input => {
                if (input !== changedInput) {
                    input.value = Math.max(parseFloat(input.value) - decreaseAmount, 0);
                }
            });
        }
    }

    /**
     * Valide une grille donne.
     *
     * @param {string[]} grid - La grille  valider, reprsente sous forme d'un tableau de chanes de caractres.
     * @returns {boolean} - Vrai si la grille est valide, faux sinon.
     */
    validateGrid(grid) {
        const rowLength = grid[0].length;
        for (const row of grid) {
            if (row.length !== rowLength || !/^[012349]+$/.test(row)) return false;
        }
        return true;
    }

    /**
     * Affiche les boutons d'une div avec un dlai de masquage.
     *
     * @param {HTMLDivElement} div - La div contenant les boutons  afficher.
     * @param {HTMLElement} principalButton - Le bouton principal qui dclenche l'affichage de la div.
     * @param {HTMLElement} parent - L'lment parent de la div et du bouton principal.
     * @returns {void}
     */
    displayDivButtons(div, principalButton, parent) {
        let moveHideTimeout; // Variable pour stocker le timeout

        // Fonction pour afficher ou masquer la div des boutons avec un dlai
        const toggleDiv = (show) => {
            moveHideTimeout = this.toggleVisibilityWithDelay(div, show, 300, moveHideTimeout);
        };

        // Affiche la div lorsque le bouton est survol
        principalButton.addEventListener('mouseover', () => toggleDiv(true));

        // Affiche la div lorsque la souris est sur la div entire
        parent.addEventListener('mouseover', () => toggleDiv(true));

        // Ferme la div avec un dlai lorsque la souris quitte la div entire
        parent.addEventListener('mouseout', (event) => {
            if (!parent.contains(event.relatedTarget)) {
                toggleDiv(false);
            }
        });
    }

    /**
     * Met  jour l'tat du bouton de dessin.
     *
     * @returns {void}
     */
    updateDrawButton() {
        HTML.drawPrincipalButton.classList.toggle('active');
        this.getApp().setEnableDraw(!this.getApp().getEnableDraw());
        this.getApp().toggleDrawingEvents();
    }

    /**
     * Bascule la visibilit d'un lment avec un dlai.
     *
     * @param {HTMLElement} element - L'lment dont la visibilit doit tre bascule.
     * @param {boolean} show - Si vrai, affiche l'lment, sinon le masque.
     * @param {number} delay - Le dlai en millisecondes avant de masquer l'lment.
     * @param {number|null} timeoutId - L'identifiant du timeout prcdent, s'il existe.
     * @returns {number|null} - L'identifiant du nouveau timeout, ou null si l'lment est affich.
     */
    toggleVisibilityWithDelay(element, show, delay, timeoutId) {
        // Annuler tout timeout prcdent
        clearTimeout(timeoutId);

        if (show) {
            element.style.display = 'flex'; // Affiche immdiatement
            return null; // Pas de nouveau timeout  grer pour l'affichage
        } else {
            return setTimeout(() => {
                element.style.display = 'none';
            }, delay); // Masque avec dlai
        }
    }

    // Fonction principale pour initialiser tous les vnements
    initialize() {
        // ICONES
        this.initializeSimulationIcon();
        this.initializeSaveButton();
        this.initializeLoadModal();
        this.initiatilizeEnterButton();
        this.initializeCopyIcon();
        this.initializePatternIcon();
        this.initializeStepIcon();
        this.initializeJumpIcon();
        this.initializeStartIcon();
        this.initializeRapidityIcon();
        this.initializeColorIcon();
        this.initializeDrawIcon();
        this.initializeTrashIcon();
        this.initializeGridIcon();
        this.initializeBordureIcon();
        this.initializeHistoryIcon();
        this.initializeArrowsButton();
        this.initializeZoomButton()
        this.initializeDefineRulesButton();
        this.initializePredefineRulesButton();
        this.initializeDocButton();
        this.initializeKeyboard();
    }

    /******************************************
     * INITIALISATION SIMULATION
     ******************************************/

    /**
     * Initialise l'icne de simulation en ajoutant un gestionnaire d'vnements au
     * bouton principal. Lorsque le bouton est cliqu, la fonction met  jour l'tat du
     * bouton de dmarrage en forant l'arrt de l'animation.
     *
     * @returns {void}
     */
    initializeSimulationIcon() {
        HTML.simulationPrincipalButton.addEventListener('click', () => {
            this.updateStartButton(true)
        });

        this.initializeConfigurerModal();
        this.initializeWeightModal();
    }

    /**
     * Initialise le modal de configuration en dfinissant un gestionnaire d'vnements sur
     * le bouton "Appliquer". Lorsque le bouton est cliqu, la fonction valide les valeurs
     * d'entre pour le nombre de lignes, de colonnes et la taille des cellules en utilisant
     * la fonction interne `validateInput`. Elle initialise ensuite une nouvelle grille en
     * nettoyant la grille actuelle, en formatant la nouvelle grille avec les valeurs valides,
     * en crant une nouvelle grille alatoire, en initialisant les grilles, en dfinissant les
     * dimensions du canvas et en dessinant la nouvelle grille.
     *
     * La fonction `validateInput` est une fonction interne qui valide une valeur d'entre en
     * fonction d'une valeur minimale et maximale autorises. Elle convertit la valeur d'entre
     * en un entier, vrifie si elle est dans la plage autorise et retourne la valeur valide.
     *
     * @returns {void}
     */
    initializeConfigurerModal() {
        function validateInput(inputElement, minValue, maxValue) {
            let value = parseInt(inputElement.value, 10);

            if (isNaN(value)) return minValue; else if (value > maxValue) return maxValue; else if (value < minValue) return minValue; else return value;
        }

        HTML.applyConfigureButton.addEventListener('click', () => {
            // Corriger les input
            let rowsInput = validateInput(HTML.rowsConfigureInput, Config.MIN_COL, Config.MAX_COL);
            let columnsInput = validateInput(HTML.columnsConfigureInput, Config.MIN_COL, Config.MAX_COL);
            let cellSizeInput = validateInput(HTML.cellSizeConfigureInput, Config.MIN_CELL_SIZE, Config.MAX_CELL_SIZE);

            // Initialiser une nouvelle grille
            this.updateStartButton(true);
            this.getApp().cleanGrid();
            this.getApp().formatGrid(rowsInput, columnsInput, cellSizeInput);
            let grid = this.getApp().getBoard().createGridRandom();
            this.getApp().getBoard().initializeGrids(grid);
            this.getApp().getBoardCanvas().setDimensionsCanvas();
            this.getApp().getBoardCanvas().drawGrid();

        });
    }

    /**
     * Initialise le modal des poids en affichant les valeurs initiales des poids et en ajoutant
     * des gestionnaires d'vnements aux entres de poids et au bouton "Appliquer". Lorsqu'une
     * valeur d'entre change, la fonction `updateWeights` est appele avec l'entre modifie comme
     * argument, et la fonction interne `displayWeights` est appele pour mettre  jour l'affichage
     * des poids dans les spans associs.
     *
     * Lorsque le bouton "Appliquer" est cliqu, la fonction cre un tableau des valeurs de poids
     * actuelles, y compris la valeur de la dernire entre calcule. Elle appelle `setWeights` sur
     * l'application avec le tableau de poids converti en nombres, et coche la case  cocher "Hasard"
     * du modal de configuration.
     *
     * La fonction `displayWeights` est une fonction interne qui affiche les valeurs actuelles des
     * poids dans les spans associs.
     *
     * @returns {void}
     */
    initializeWeightModal() {
        const displayWeights = () => {
            HTML.weightFullRandomInputs.forEach((input, index) => {
                HTML.weightRandomSpans[index].textContent = `(${input.value})`;
            });
        }

        // Affiche la valeur initiale des poids
        HTML.weightFullRandomInputs.forEach((input, index) => {
            HTML.weightRandomSpans[index].textContent = `(${input.value})`;
        });

        HTML.weightFullRandomInputs.forEach(input => {
            input.addEventListener('input', () => {
                this.updateWeights(input);
                displayWeights();
            });
        });

        HTML.applyRandombutton.addEventListener('click', () => {
            const weights = [];
            weights.push(HTML.weightRandomInput0.value);
            HTML.weightFullRandomInputs.forEach(weight => {
                weights.push(weight.value);
            });
            this.getApp().setWeights(weights.map(Number));
            HTML.hasardConfigureCheckbox.checked = true;
        })
    }

    /******************************************
     * INITIALISATION SAUVEGARDE
     ******************************************/

    initializeSaveButton() {
        HTML.savePrincipalButton.addEventListener('click', () => {

            // Mettre en pause l'animation
            this.getApp().stopAnimation();
            this.updateStartButton(true);
        })

        this.initializeSaveModal();
    }

    /**
     * Initialise le modal de sauvegarde en ajoutant un gestionnaire d'vnements au bouton
     * "Sauvegarder". Lorsque le bouton est cliqu, la fonction met en pause l'animation, met
     *  jour le bouton de dmarrage et rcupre le nom de sauvegarde entr dans l'input. Si
     * le champ est vide, elle demande  l'utilisateur d'entrer un nom.
     *
     * La fonction vrifie ensuite s'il existe dj une sauvegarde avec le mme nom. Si c'est
     * le cas, elle affiche une bote de dialogue de confirmation pour remplacer la sauvegarde
     * existante. Enfin, elle enregistre la grille actuelle dans le localStorage sous le nom de
     * sauvegarde et met  jour la liste des noms de sauvegarde.
     *
     * @returns {void}
     */
    initializeSaveModal() {
        HTML.applySaveButton.addEventListener('click', () => {
            let saveName = HTML.nameSaveInput.value.toLowerCase().trim();
            while (!saveName) {
                saveName = prompt("Veuillez entrer un nom pour la sauvegarde.").toLowerCase().trim();
            }
            const saveData = this.getApp().getBoard().getGrid();

            // Vrifier s'il existe dj une sauvegarde avec le mme nom
            let saveNames = JSON.parse(localStorage.getItem('saveNames')) || [];
            let saves = JSON.parse(localStorage.getItem('savesLifeGame')) || {};

            if (saves[saveName]) {
                if (!confirm(`Une sauvegarde portant le mme nom existe dj. Voulez-vous la remplacer ?`)) {
                    return;
                }
            } else { saveNames.push(saveName);}

            saves[saveName] = saveData;
            localStorage.setItem('saveNames', JSON.stringify(saveNames));
            localStorage.setItem('savesLifeGame', JSON.stringify(saves));
            HTML.nameSaveInput.value = '';
        });
    }

    /******************************************
     * INITIALISATION CHARGEMENT
     ******************************************/

    /**
     * Initialise le modal de chargement en ajoutant un gestionnaire d'vnements au bouton
     * "Charger". Lorsque le bouton est cliqu, la fonction met en pause l'animation, met 
     * jour le bouton de dmarrage, efface les options du select de chargement et remplit le
     * select avec les noms de sauvegarde rcuprs  partir du localStorage.
     *
     * Lorsque le bouton "Appliquer" du modal de chargement est cliqu, la fonction rcupre
     * la grille sauvegarde correspondant  l'option slectionne dans le select, initialise
     * une nouvelle grille en nettoyant la grille actuelle, en formatant la nouvelle grille
     * avec les dimensions de la grille sauvegarde, en initialisant les grilles avec les
     * valeurs sauvegardes, en dfinissant les dimensions du canvas et en dessinant la nouvelle
     * grille.
     *
     * @returns {void}
     */
    initializeLoadModal() {
        HTML.loadPrincipalButton.addEventListener('click', () => {

            // Mettre en pause l'animation
            this.getApp().stopAnimation();
            this.updateStartButton(true);

            // Effacer le select
            HTML.configLoadSelect.innerHTML = '';

            // Rcuprer les noms des sauvegardes  partir de l'objet 'saveNames'
            let saveNames = JSON.parse(localStorage.getItem('saveNames')) || [];

            // Crer le select
            saveNames.forEach(saveName => {
                const option = document.createElement('option');
                option.value = saveName;
                option.textContent = saveName;
                HTML.configLoadSelect.appendChild(option);
            });
        });

        HTML.applyLoadButton.addEventListener('click', () => {
            const selectedSave = HTML.configLoadSelect.value;

            // Rcuprer les sauvegardes  partir de l'objet 'savesLifeGame'
            let saves = JSON.parse(localStorage.getItem('savesLifeGame')) || {};
            const savedGrid = saves[selectedSave];

            // Initialiser une nouvelle grille
            this.updateStartButton(true);
            this.getApp().cleanGrid();
            this.getApp().formatGrid(savedGrid.length, savedGrid[0].length, this.getApp().getCellSize());
            this.getApp().getBoard().initializeGrids(savedGrid);
            this.getApp().getBoardCanvas().setDimensionsCanvas();
            this.getApp().getBoardCanvas().drawGrid();
        });
    }

    /******************************************
     * INITIALISATION INSERTION ET COPIE
     ******************************************/

    initiatilizeEnterButton() {
        this.initializeEnterModal();
    }

    /**
     * Initialise le modal d'entre de grille en ajoutant un gestionnaire d'vnements  la zone
     * de texte d'entre de grille. Lorsque la valeur de la zone de texte change, la fonction
     * valide la grille en utilisant la mthode `validateGrid`. Si la grille est valide, elle
     * ajoute la classe CSS "valid"  la zone de texte et masque le message d'erreur. Sinon,
     * ell ajoute la classe CSS "invalid"  la zone de texte et affiche le message d'erreur. Le
     * bouton "Appliquer" est galement dsactiv tant que la grille n'est pas valide.
     *
     * Lorsque le bouton "Appliquer" est cliqu, si la grille est valide, la fonction initialise
     * une nouvelle grille en nettoyant la grille actuelle, en formatant la nouvelle grille avec
     * les dimensions de la grille entre, en initialisant les grilles avec les valeurs entres,
     * en dfinissant les dimensions du canvas et en dessinant la nouvelle grille.
     *
     * @returns {void}
     */
    initializeEnterModal() {
        let isValidatedGrid;
        let grid;

        HTML.arrayGridEnterInput.addEventListener('input', () => {
            grid = HTML.arrayGridEnterInput.value.trim().split('\n')
            isValidatedGrid = this.validateGrid(grid);
            if (isValidatedGrid) {
                HTML.arrayGridEnterInput.classList.add('valid');
                HTML.arrayGridEnterInput.classList.remove('invalid');
                HTML.errorGridEnterP.classList.add('d-none');
            } else {
                HTML.arrayGridEnterInput.classList.add('invalid');
                HTML.arrayGridEnterInput.classList.remove('valid');
                HTML.errorGridEnterP.classList.remove('d-none');
            }

            HTML.applyGridEnterButton.disabled = !isValidatedGrid;
        });

        HTML.applyGridEnterButton.addEventListener("click", () => {
            if (isValidatedGrid) {
                grid = grid.map(row => Array.from(row, Number));

                // Initialiser une nouvelle grille
                this.updateStartButton(true);
                this.getApp().cleanGrid();
                this.getApp().formatGrid(grid.length, grid[0].length, this.getApp().getCellSize());
                this.getApp().getBoard().initializeGrids(grid);
                this.getApp().getBoardCanvas().setDimensionsCanvas();
                this.getApp().getBoardCanvas().drawGrid();
            }
        });
    }


    /**
     * Initialise l'icne de copie en ajoutant un gestionnaire d'vnements au bouton principal.
     * Lorsque le bouton est cliqu, la fonction rcupre la valeur de la grille actuelle, la
     * convertit en une chane de caractres et la copie dans le presse-papier  l'aide de l'API
     * Clipboard. Un message d'alerte est affich en cas de succs ou d'chec de la copie.
     *
     * @returns {void}
     */
    initializeCopyIcon() {
        HTML.copyPrincipalButton.addEventListener('click', () => {
            const gridValue = this.getApp()
                .getBoard()
                .getGrid()
                .map(row => row.join(''))
                .join('\n');

            // Crer un text area pour effectuer la copie
            const textArea = document.createElement('textarea');
            textArea.style.position = 'fixed';
            textArea.style.opacity = '0';
            textArea.style.left = '-9999px';
            textArea.style.top = '-9999px';
            textArea.value = gridValue;

            // Ajouter le textarea au document
            document.body.appendChild(textArea);

            // Slectionner le texte
            textArea.select();
            textArea.setSelectionRange(0, textArea.value.length); // Pour les appareils mobiles

            navigator.clipboard.writeText(gridValue)
                .then(() => {
                    alert('Le texte a t copi dans le presse-papier.');
                })
                .finally(() => {
                    document.body.removeChild(textArea);
                });
        });
    }

    /******************************************
     * INITIALISATION DES PATTERNS
     ******************************************/

    initializePatternIcon() {
        const patternManager = new PatternManager(this);
        patternManager.init();
    }


    /******************************************
     * INITIALISATION PAS A PAS
     ******************************************/

    /**
     * Initialise l'icne d'tape en ajoutant un gestionnaire d'vnements au bouton principal.
     * Lorsque le bouton est cliqu, la fonction calcule la prochaine gnration de la grille
     * en appelant la mthode `calculateNextGeneration` de l'application.
     *
     * @returns {void}
     */
    initializeStepIcon() {
        HTML.stepPrincipalButton.addEventListener('click', () => {
            this.getApp().calculateNextGeneration();
        });
    }

    /**
     * Initialise l'icne de saut en ajoutant un gestionnaire d'vnements aux boutons de saut. Lorsqu'un
     * bouton de saut est cliqu, la fonction met  jour les classes CSS des boutons pour indiquer la
     * slection, dfinit la valeur de saut dans l'application, ferme immdiatement la div des boutons
     * de saut et met  jour l'tat du bouton de dmarrage.
     *
     * La fonction `displayDivButtons` est appele pour afficher et masquer la div des boutons de saut
     * avec un dlai.
     *
     * @returns {void}
     */
    initializeJumpIcon() {
        this.displayDivButtons(HTML.jumpDiv, HTML.jumpPrincipalButton, HTML.jumpParent);

        // Grer le clic sur les boutons du saut
        HTML.jumpButtons.forEach(jumpButton => {
            jumpButton.addEventListener('click', () => {
                // Mettre  jour les classes des boutons pour indiquer la slection
                HTML.jumpButtons.forEach(button => button.classList.remove('bg-success', 'text-light'));
                jumpButton.classList.add('bg-success', 'text-light');

                // Mettre  jour la vitesse dans app
                this.getApp().setStep(jumpButton.dataset.jump);

                // Fermer la div immdiatement aprs le clic
                HTML.jumpDiv.style.display = 'none';

                // Apeller updateStartButton pour mettre  jour l'tat
                this.updateStartButton(true);
            });
        });
    }

    /******************************************
     * INITIALISATION ANIMATION
     ******************************************/

    /**
     * Initialise l'icne de dmarrage en ajoutant un gestionnaire d'vnements au bouton
     * principal. Lorsque le bouton est cliqu, la fonction met  jour l'tat du bouton de
     * dmarrage sans forcer l'arrt de l'animation.
     *
     * @returns {void}
     */
    initializeStartIcon() {
        HTML.startPrincipalButton.addEventListener('click', () => {
            this.updateStartButton(false);
        });
    }

    /**
     * Initialise l'icne de rapidit en ajoutant un gestionnaire d'vnements aux boutons de rapidit.
     * Lorsqu'un bouton de rapidit est cliqu, la fonction met  jour les classes CSS des boutons pour
     * indiquer la slection, dfinit la valeur de rapidit dans l'application, ferme immdiatement la
     * div des boutons de rapidit et met  jour l'tat du bouton de dmarrage.
     *
     * La fonction `displayDivButtons` est appele pour afficher et masquer la div des boutons de
     * rapidit avec un dlai.
     *
     * @returns {void}
     */
    initializeRapidityIcon() {
        this.displayDivButtons(HTML.rapidityDiv, HTML.rapidityPrincipalButton, HTML.rapidityParent);

        // Grer le clic sur les boutons de rapidit
        HTML.rapidityButtons.forEach(rapidityButton => {
            rapidityButton.addEventListener('click', () => {
                // Mettre  jour les classes des boutons pour indiquer la slection
                HTML.rapidityButtons.forEach(button => button.classList.remove('bg-success', 'text-light'));
                rapidityButton.classList.add('bg-success', 'text-light');

                // Mettre  jour la vitesse dans app
                this.getApp().setRapidity(rapidityButton.dataset.rapidity);

                // Fermer la div immdiatement aprs le clic
                HTML.rapidityDiv.style.display = 'none';

                // Apeller updateStartButton pour mettre  jour l'tat
                this.updateStartButton(true);
            });
        });
    }

    /******************************************
     * INITIALISATION COULEUR ET DESSIN
     ******************************************/
    /**
     * Initialise l'icne de couleur en ajoutant un gestionnaire d'vnements aux boutons de couleur.
     * Lorsqu'un bouton de couleur est cliqu, la fonction met  jour la classe CSS du bouton principal
     * de couleur, dfinit la valeur d'ajout dans l'application, ferme immdiatement la div des boutons
     * de couleur et met  jour l'affichage des icnes de couleur et de mur en fonction de la couleur
     * slectionne.
     *
     * La fonction `displayDivButtons` est appele pour afficher et masquer la div des boutons de
     * couleur avec un dlai.
     *
     * @returns {void}
     */
    initializeColorIcon() {
        this.displayDivButtons(HTML.colorDiv, HTML.colorPrincipalButton, HTML.colorParent);

        // Grer le clic sur les boutons de couleur
        HTML.colorButtons.forEach(colorButton => {
            colorButton.addEventListener('click', () => {

                HTML.colorPrincipalButton.classList.remove('blue', 'green', 'yellow', 'red');
                let className = colorButton.dataset.color;
                this.getApp().setValueAdd(parseInt(colorButton.dataset.value));

                HTML.colorPrincipalButton.classList.add(className);

                // Fermer la div immdiatement aprs le clic
                HTML.colorDiv.style.display = 'none';

                const isWall = colorButton.dataset.color === 'wall';
                HTML.colorIcon.style.display = isWall ? 'none' : 'block';
                HTML.wallIcon.style.display = isWall ? 'block' : 'none';
            });
        });
    }

    /**
     * Initialise l'icne de dessin en ajoutant un gestionnaire d'vnements au bouton principal.
     * Lorsque le bouton est cliqu, la fonction met  jour l'tat du bouton de dmarrage en
     * forant l'arrt de l'animation, puis appelle la mthode `updateDrawButton` pour mettre 
     * jour l'tat du mode de dessin.
     *
     * @returns {void}
     */
    initializeDrawIcon() {
        HTML.drawPrincipalButton.addEventListener('click', () => {
            this.getApp().stopAnimation();
            this.updateStartButton(true);
            this.updateDrawButton();
        });
    }

    /******************************************
     * INITIALISATION CORB, GRILLE, BORD, HIST
     ******************************************/

    /**
     * Initialise l'icne de corbeille en ajoutant un gestionnaire d'vnements au bouton
     * principal. Lorsque le bouton est cliqu, la fonction nettoie la grille en appelant
     * la mthode `cleanGrid` de l'application.
     *
     * @returns {void}
     */
    initializeTrashIcon() {
        HTML.trashPrincipalButton.addEventListener('click', () => {
            this.getApp().cleanGrid();
            this.getApp().stopAnimation();
            this.updateStartButton(true);
        });
    }

    /**
     * Initialise l'icne de grille en ajoutant un gestionnaire d'vnements au bouton principal.
     * Lorsque le bouton est cliqu, la fonction bascule l'tat de l'affichage des lignes de la
     * grille dans l'application en appelant les mthodes `setLines` et `getLines`. Si les lignes
     * sont dsactives, elle efface le canvas en appelant `clearCanvas`.
     * Enfin, elle redessine la grille en appelant `drawGrid`.
     *
     * @returns {void}
     */
    initializeGridIcon() {
        HTML.gridPrincipalButton.addEventListener('click', () => {
            HTML.gridPrincipalButton.classList.toggle('active');
            this.getApp().setLines(!this.getApp().getLines());
            if (!this.getApp().getLines()) this.getApp().getBoardCanvas().clearCanvas();
            this.getApp().getBoardCanvas().drawGrid();

        });
    }

    /**
     * Initialise l'icne de bordure en ajoutant un gestionnaire d'vnements au bouton principal.
     * Lorsque le bouton est cliqu, la fonction bascule l'tat de l'affichage de la bordure du
     * canvas dans l'application en appelant les mthodes `setBorder` et `getBorder`. Elle ajoute
     * ou supprime galement la classe CSS "canvas-border" sur le canvas.
     *
     * @returns {void}
     */
    initializeBordureIcon() {
        HTML.borderPrincipalButton.addEventListener('click', () => {
            HTML.borderPrincipalButton.classList.toggle('active');

            this.getApp().setBorder(!this.getApp().getBorder());
            $('canvas').classList.toggle('canvas-border');
        });
    }

    /**
     * Initialise l'icne d'historique en ajoutant un gestionnaire d'vnements au bouton
     * principal. Lorsque le bouton est cliqu, la fonction bascule l'tat de l'historique
     * dans l'application en appelant les mthodes `setHistory` et `getHistory`. Si l'historique
     * est dsactiv, elle efface le canvas en appelant `clearCanvas`.
     * Enfin, elle redessine la grille en appelant `drawGrid`.
     *
     * @returns {void}
     */
    initializeHistoryIcon() {
        HTML.historyPrincipalButton.addEventListener('click', () => {
            HTML.historyPrincipalButton.classList.toggle('active');
            this.getApp().setHistory(!this.getApp().getHistory());
            if (!this.getApp().getHistory()) {
                this.getApp().getBoardCanvas().clearCanvas();
            }
            this.getApp().getBoardCanvas().drawGrid();

        });
    }

    /******************************************
     * INITIALISATION DEPLACEMENT
     ******************************************/

    /**
     * Initialise les boutons de dplacement en ajoutant un gestionnaire d'vnements aux boutons
     * de dplacement. Lorsqu'un bouton de dplacement est cliqu, la fonction appelle la mthode
     * `updateArrows` avec la direction correspondante.
     *
     * La fonction `displayDivButtons` est appele pour afficher et masquer la div des boutons de
     * dplacement avec un dlai.
     *
     * @returns {void}
     */
    initializeArrowsButton() {
        this.displayDivButtons(HTML.moveDiv, HTML.movePrincipalButton, HTML.moveParent);

        HTML.moveButtons.forEach(moveButton => {
            moveButton.addEventListener('click', () => {
                this.updateArrows(moveButton.dataset.direction);
            });
        });
    }

    /**
     * Met  jour la position de la grille dans une direction donne en appelant la mthode
     * correspondante de la grille, puis redessine la grille.
     *
     * @param {string} direction - La direction dans laquelle dplacer la grille ('Up', 'Down',
     * 'Left' ou 'Right').
     * @returns {void}
     */
    updateArrows(direction) {
        const buttonName = direction.charAt(0).toUpperCase() + direction.slice(1);
        this.getApp().getBoard()[`move${buttonName}`]();
        this.getApp().getBoardCanvas().drawGrid();
    }

    /******************************************
     * INITIALISATION ZOOM
     ******************************************/

    /**
     * Initialise le bouton de zoom en ajoutant un gestionnaire d'vnements aux boutons de zoom.
     * Lorsqu'un bouton de zoom est cliqu, la fonction appelle la mthode `updateZoom` avec la
     * valeur de zoom correspondante.
     *
     * La fonction `displayDivButtons` est appele pour afficher et masquer la div des boutons de
     * zoom avec un dlai.
     *
     * @returns {void}
     */
    initializeZoomButton() {
        this.displayDivButtons(HTML.zoomDiv, HTML.zoomPrincipalButton, HTML.zoomParent);

        HTML.zoomButtons.forEach(zoomButton => {
            zoomButton.addEventListener('click', () => {
                this.updateZoom(zoomButton.dataset.value)
            });
        });
    }

    /**
     * Met  jour le zoom de la grille en appelant la mthode correspondante de l'application.
     *
     * @param {string} zoom - La valeur de zoom  appliquer ('in' ou 'out').
     * @returns {void}
     */
    updateZoom(zoom) {
        const buttonName = zoom.charAt(0).toUpperCase() + zoom.slice(1)
        this.getApp()[`setCellSizeZoom${buttonName}`]();
    }

    /******************************************
     * INITIALISATION REGLES
     ******************************************/

    initializeDefineRulesButton() {
        HTML.defineRulesPrincipalButton.addEventListener('click', () => {
            // Mettre en pause l'animation
            this.getApp().stopAnimation();
            this.updateStartButton(true);
        })

        this.initializeDefineRulesModal();
    }

    /**
     * Initialise le modal de dfinition des rgles en ajoutant un gestionnaire d'vnements au
     * bouton "Appliquer". Lorsque le bouton est cliqu, la fonction rcupre les cases  cocher
     * coches pour les rgles de naissance et de survie, les convertit en ensembles de nombres
     * et les dfinit dans l'application en utilisant les mthodes `setBirth` et `setSurvival`.
     *
     * @returns {void}
     */
    initializeDefineRulesModal() {
        HTML.applyDefineRulesButton.addEventListener('click', () => {
            this.getApp().setBirth(new Set(Array.from(HTML.birthDefineRulesCheckboxes)
                .filter(checkbox => checkbox.checked)
                .map(checkbox => parseInt(checkbox.value))));
            this.getApp().setSurvival(new Set(Array.from(HTML.survivalDefineRulesCheckboxes)
                .filter(checkbox => checkbox.checked)
                .map(checkbox => parseInt(checkbox.value))));
        });
    }

    initializePredefineRulesButton() {
        HTML.predefineRulesPrincipalButton.addEventListener('click', () => {
            // Mettre en pause l'animation
            this.getApp().stopAnimation();
            this.updateStartButton(true);
        })

        this.initializePredefineRulesModal();
    }

    /**
     * Initialise le modal des rgles prdfinies en ajoutant un gestionnaire d'vnements au
     * bouton "Appliquer". Lorsque le bouton est cliqu, la fonction rcupre les rgles slection-
     * nes dans le select, les convertit en ensembles de nombres pour les rgles de naissance et
     * de survie, vrifie la validit des rgles entres et les dfinit dans l'application en
     * utilisant les mthodes `setBirth` et `setSurvival`.
     *
     * @returns {void}
     */
    initializePredefineRulesModal() {
        HTML.applyPredefineRulesButton.addEventListener('click', () => {
            this.getApp().verifyInputRules();
            const [births, survivals] = HTML.predefineRulesSelect.value
                .split('/')
                .map(valeurs => new Set(valeurs.split('').map(Number)));
            this.getApp().setBirth(births);
            this.getApp().setSurvival(survivals);
            this.getApp().verifyInputRules();
        });
    }

    /******************************************
     * INITIALISATION CLAVIER
     ******************************************/

    /**
     * Initialise les gestionnaires d'vnements clavier pour le zoom et le dplacement de la
     * grille.
     *
     * Lorsque les touches Ctrl++ ou Ctrl+- sont enfonces, la fonction appelle la mthode
     * `updateZoom` avec la valeur de zoom correspondante.
     *
     * Lorsque les touches flches sont enfonces, la fonction appelle la mthode `updateArrows`
     * avec la direction correspondante.
     *
     * @returns {void}
     */
    initializeKeyboard() {
        document.addEventListener('keydown', (event) => {
            if (event.ctrlKey && ['+', '-'].includes(event.key)) {
                event.preventDefault();
                this.updateZoom(event.key === '+' ? 'In' : 'Out')
            }

            if (['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'].includes(event.key)) {
                event.preventDefault();
                this.updateArrows(event.key.slice(5));
            }
        });
    }

    /******************************************
     * INITIALISATION DOCUMENTATION
     ******************************************/

    /**
     * Initialise le bouton de basculement entre la fentre du jeu et la fentre de documentation.
     *
     * Lorsque le bouton est cliqu, la fonction bascule les classes CSS des fentres pour les
     * afficher ou les masquer, et met  jour l'icne du bouton en fonction de l'tat actuel.
     *
     * @returns {void}
     */
    initializeDocButton() {
        HTML.toggleDocButton.addEventListener('click', function () {
            if (HTML.gameWindow.classList.contains('desactivate')) {
                HTML.gameWindow.classList.remove('desactivate');
                HTML.docWindow.classList.add('desactivate')
                HTML.toggleDocIcon.className = 'bi bi-question-circle-fill';
            } else {
                HTML.gameWindow.classList.add('desactivate');
                HTML.docWindow.classList.remove('desactivate')
                HTML.toggleDocIcon.className = 'bi bi-controller';
            }
        });
    }
}